"use server"

import { createContext, useContext, useState, useEffect, type ReactNode } from "react"
import type { User } from "./types"
import { query } from "./db" // Assuming './db' handles database interactions
import bcrypt from "bcrypt"


interface AuthContextType {
  user: User | null
  isLoading: boolean
  login: (email: string, password: string) => Promise<boolean>
  signup: (email: string, password: string, name: string) => Promise<boolean>
  loginWithProvider: (provider: "google" | "facebook" | "github") => Promise<boolean>
  logout: () => void
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Check for existing session
    const savedUser = localStorage.getItem("user")
    if (savedUser) {
      setUser(JSON.parse(savedUser))
    }
    setIsLoading(false)
  }, [])

  const login = async (email: string, password: string): Promise<boolean> => {
    const result: any = await query("SELECT * FROM users WHERE email = ?", [email])
    const existingUser = result[0]

    if (existingUser) {
      const passwordMatch = await bcrypt.compare(password, existingUser.password)
      if (passwordMatch) {
        const { password: _, ...userWithoutPassword } = existingUser
        setUser(userWithoutPassword)
        localStorage.setItem("user", JSON.stringify(userWithoutPassword))
        return true
      }
    }
    return false
  }

  const signup = async (email: string, password: string, name: string): Promise<boolean> => {
    const result: any = await query("SELECT * FROM users WHERE email = ?", [email])
    const existingUser = result[0]

    if (existingUser) {
      return false
    }

    const hashedPassword = await bcrypt.hash(password, 10)
    const insertResult: any = await query(
      "INSERT INTO users (name, email, password, provider) VALUES (?, ?, ?, ?)",
      [name, email, hashedPassword, "email"]
    )

    if (insertResult.affectedRows > 0) {
      const newUser: User = {
        id: insertResult.insertId.toString(),
        email,
        name,
        provider: "email",
        createdAt: new Date().toISOString(),
      }
      setUser(newUser)
      localStorage.setItem("user", JSON.stringify(newUser))
      return true
    }

    return false
  }

  const loginWithProvider = async (provider: "google" | "facebook" | "github"): Promise<boolean> => {
    // Simulate OAuth login - in production, this would redirect to OAuth provider
    const mockUser: User = {
      id: Date.now().toString(),
      email: `user@${provider}.com`,
      name: `${provider.charAt(0).toUpperCase() + provider.slice(1)} User`,
      provider,
      createdAt: new Date().toISOString(),
    }

    setUser(mockUser)
    localStorage.setItem("user", JSON.stringify(mockUser))
    return true
  }

  const logout = () => {
    setUser(null)
    localStorage.removeItem("user")
  }

  return (
    <AuthContext.Provider value={{ user, isLoading, login, signup, loginWithProvider, logout }}>
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}
